import React, { createContext, useContext } from 'react';

const ProjectContext = createContext();
export const useProjectContext = () => useContext(ProjectContext);

export const ProjectProvider = ({ children }) => {
  const initialState = {
    projects: [   
      { 
        id: 1,
        name: 'Run Buddy',
        description:'is an individual project that is mainly focused on HTML, CSS, and other frontend web development concepts.',
        img: require('../assets/projectPic/2.jpg').default,
        link:"https://gewalex.github.io/run-buddy/"
      },
      {
        id: 2,
        name: 'Dominate Your Day',
        description:'is a 3-person group project that has three main features. Its a website that offers weather updates, news, and a scheduler',
        img: require('../assets/projectPic/3.jpg').default,
        link:"https://gewalex.github.io/dominate-your-day/"
      }
    ]
  };
  return (
    <ProjectContext.Provider value={initialState}>
      {children}
    </ProjectContext.Provider>
  );
};
