import React from 'react'
import "./MobMenu.css"
import {useStoreContext} from "../utils/MobMenuState"
import {TOGGLE_MOBMENU} from "../utils/actions"
import { Link } from 'react-scroll'
import { BiMenu } from 'react-icons/bi'
import {RxCross2} from "react-icons/rx"

const MobMenu = () => {
    const [state, dispatch] = useStoreContext()

    function toggleMobMenu() {
        dispatch({type: TOGGLE_MOBMENU})
    }

    if(!state.isMobMenuOpen) {
        return(
            <div className="menu-closed" onClick={toggleMobMenu}>
                <BiMenu/>
            </div>
        )
    }
  return (
    <div className='menu'>
     <div className='close' onClick={toggleMobMenu}>
        <RxCross2 size={30}/>
     </div>
        <h2>Menu</h2>
     <div className='menu-col'>
        <Link to="Portfolio" spy={true} smooth={true}>
            <span> Portfolio</span> 
        </Link>
        <Link to="Resume" spy={true} smooth={true} >
            Resume
        </Link>
        <a href="mailto:wagerve@gmail.com">Contact</a> 
     </div>
    </div>  
  )
}

export default MobMenu
