import React from 'react'
import "./Resume.css"
import wgResume from"./wgResume.pdf"

const Resume = () => {
  return (
    <div className='res-resume'>
      <div className='flex-row'>
        <h2 className='res-title res-border'>
      {/* Frontend and Backend Proficiencies */}
            Proficiencies
        </h2>
      </div>
      <div className='res-cv'>
        <h3>
            {/* Download my resume */}
          <a href={wgResume} download>
            <button className="res-button">Download my CV</button>
          </a>
        </h3>
        <div className='res-prof'>
          <div className='res-front-prof'>
            <h4>Frontend</h4>
            <ul>
                <li>HTML</li>
                <li>CSS</li>
                <li>JavaScript</li>
                <li>React</li>
                <li>Java</li>
            </ul>
          </div>
          <div className='res-back-prof'>
            <h4>Backend</h4>
            <ul>
                <li>MongoDB</li>
                <li>NodeJS</li>
                <li>Express</li>
                <li>GraphQL</li>
                <li>RESTful API</li>
            </ul>
          </div>
        </div>
       </div>
      </div>
  )
}

export default Resume
