import React from 'react'
import "./Navbar.css"
import { Link } from 'react-scroll'

const Navbar = () => {
  return (
     <div className='nav-header'>
      <h1>W GERVE</h1>
      <div className="nav-navbar">
        <ul>
          <li><Link to="/Navbar"spy={true} smooth={true} >Home</Link></li>
          <li><Link to="Portfolio" spy={true} smooth={true}>Portfolio</Link></li>
          <li><Link to="Resume" spy={true} smooth={true} >Resume</Link></li>
          <li><a href="mailto:wagerve@gmail.com">Contact</a></li>
        </ul>
      </div>
    </div>  
  )
}

export default Navbar
